/*
 * Pagination Button
 */
.pagination-button {
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  background-color: transparent;
  border-color: $COOL_GRAY_LIGHT;
  border-style: solid;
  border-width: 2px;
  border-radius: 33px;
  color: $COOL_GRAY_LIGHT;
  cursor: pointer;
  display: inline-block;
  font-family: Kievit-Medium;
  font-size: 13px;
  letter-spacing: 0.04em;
  line-height: 16.21px;
  overflow: hidden;
  padding: 5px 20px;
  &__dot-row {
    display: block;
    list-style: none;
    margin: 0 auto;
    padding: 0;
    &__element {
      border: 3px solid $COOL_GRAY_LIGHT;
      border-radius: 100%;
      display: inline-block;
      margin: 0 4px 0 0;
      vertical-align: 1px;
    }
    &__number {
      display: inline-table;
      margin: 0 4px 0 0;
    }
  }
  &--hide {
    display: none;
  }
}

