&-Buttons {
  @include min-screen(769px) {
    right: 0px;
  }

  @include min-screen(1025px) {
    right: 20px;  
  }

  .SubscribeButton,
  .MyNyplButton {
    @include Kievit-Bold;
    font-size: 12px;
    color: #000;

    &.active {
      background-color: #1980A9;
      color: #FFF;
      -webkit-transition: background-color .2s linear 0s, color .2s linear 0s; /* Safari */
      transition: background-color .2s linear 0s, color .2s linear 0s;

      &:visited {
        color: #FFF;
      }
    }

    &:visited {
      color: #000;
    }
      
    .icon {
      opacity: .8;
    }
  }
}

.EmailSubscription-Wrapper {
  text-transform: none;
  display: none;

  &.active {
    display: block;
  }

  .EmailSubscription {
    .SubscribeMessageBox {
      width: 85%;

      &-Eyebrow {
        border-top: 2px solid #FFF;
        color: #fff;
        margin-bottom: 10px;
        width: 25px;
      }
      &-Title {
        color: #fff;
        font-size: 20px;
        @include Kievit-Book;
        font-weight: normal;
        line-height: 1.2em;

        &-BestNYPL {
          @include Kievit-Medium;
          font-weight: normal;
        }
      }
    }

    &-NewEmail {
      margin-top: 20px;
      font-size: 12px;
      text-transform: uppercase;
      @include Kievit-Light;

      a {
        border-bottom: 1px solid #fff;
        color: #fff;
      }
    }

    &-SocialMediaWidget {
      ul {
        list-style-type: none;
        margin: 0;
        padding: 0;

        li {
          display: inline-block;
          margin: 0 15px 0 0;
          line-height: 0;

          &:last-child {
            margin-right: 0;
          }

          a {
            color: #fff;
            text-decoration: none;
            display: block;
            font-size: 55px;
          }
        }
      }
    }

    &-FollowUs {
      color: #fff;
      text-transform: uppercase;
      margin-top: 40px;
      font-size: 12px;
    }
    
    &-Misc-Error {
      color: #fff;
      font-size: 20px;
      line-height: 1.2em;
      padding-top: 40px;
      @include Kievit-Book;

      .icon {
        vertical-align: middle;
        font-size: 25px;
        margin-right: 5px;
      }
    }
  }

  .EmailSubscribeForm {
    &-Error {
      @include Kievit-Light;
      display: none;
      position: absolute;
      margin-top: 15px;
      margin-left: 5px;
      font-size: 13px;
      color: #fff;

      &.active {
        display: block;
      }

      .icon {
        color: rgba(0, 0, 0, 0.5);
        font-size: 22px;
        margin-right: 5px;
        vertical-align: middle;
      }
    }
    &-Input {
      display: table-cell;
      border-radius: 5px;
      padding: 2px 5px;
      height: 35px;
      margin: 15px 0 0 0;
      width: 95%;
      font-size: 14.5px;
      border: 3px solid #1DA1D4;
      color: #837e77;
      @include Kievit-Book-Italic;

      &:focus {
        border: 3px solid #1980A9;
      }
    }
    &-Submit {
      .icon {
        color: #fff;
        font-size: 30px;
        position: absolute;
        margin-top: 53px;
        margin-left: 5px;
      }
    }
  }
}


